@import 'src/components/styles/helpers.scss';

.form {
  width: 100%;
}

.bot {
  margin-top: rem(34px);
  @media #{media("<")} {
    margin-top: rem(24px);

    display: block;
  }
  display: flex;
  justify-content: space-between;
}

.item {

  &:not(:first-child) {
    margin-top: rem(44px);
    @media #{media("<")} {
      margin-top: rem(24px);
    }
  }
}

.input {
  padding-bottom: rem(20px);
  @media #{media("<")} {
    padding-bottom: rem(16px);
  }
  width: 100%;

  border: none;
  color: color(black);
  border-bottom: 1px solid color(borders);

  @include placeholder {
    color: color(black);
  }
}

.button {
  @media #{media("<")} {
    margin-top: rem(16px);

    display: flex;
    justify-content: flex-end;
  }
}