@import 'src/components/styles/helpers.scss';

.row {
  display: flex;
  @media #{media("<")} {
    justify-content: space-between;
  }
  @media #{media("<", m)} {
    display: block;
  }
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    padding-right: $col-gap-d;
    @media #{media("<")} {
      padding-right: 0;
      width: calc(#{$col-part-m} * 6 - #{$col-gap-m});
    }
    width: calc(#{$col-part-d} * 5);
    @media #{media("<", m)} {
      width: auto;
    }
  }

  &:nth-child(2) {
    flex-grow: 1;
    @media #{media("<")} {
      width: calc(#{$col-part-m} * 6 - #{$col-gap-m});
    }
    @media #{media("<", m)} {
      margin-top: rem(32px);
      width: auto;
      display: flex;
    }
  }
}

.item {
  position: relative;
  z-index: 0;
  padding-top: rem(6px);
  padding-left: rem(13px);
  @media #{media("<")} {
    padding-top: rem(3px);
    padding-left: rem(7px);
  }

  @include fsl(32px, 100%);
  letter-spacing: -0.04em;
  @media #{media("<")} {
    @include fsl(16px);
  }

  &:not(:first-child) {
    margin-top: rem(24px);
    @media #{media("<")} {
      margin-top: 8px;
    }
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;

    width: rem(6px);
    height: rem(6px);
    @media #{media("<")} {
      width: rem(4px);
      height: rem(4px);
    }

    background-color: color(black);
    border-radius: 50%;
  }
}

.stat {

  &:not(:first-child) {
    margin-top: rem(100px);
    @media #{media("<")} {
      margin-top: rem(32px);
    }
    @media #{media("<", m)} {
      margin-top: 0;
    }
  }

  @media #{media("<", m)} {
    &:nth-child(1) {
      width: calc(#{$col-part-m} * 8);
    }
  }
}

.text {
  line-height: 100%;
}

.number {
  // margin-bottom: rem(-30px);
  display: flex;
}

.wrapper {
  position: relative;
  z-index: 0;
}

.title {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
}

.layout {
  opacity: 0;
}