@import 'src/components/styles/helpers.scss';

.layout {
  position: fixed;
  z-index: -1;
  left: 0;
  top: 0;

  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  pointer-events: none;
  overflow: hidden;
}

.video {
  position: absolute;
  @media #{media("<")} {
    // top: 50%;
    // transform: translateY(-50%);
    width: 100%;
  }
  min-width: 100vw;
  min-height: 100vh;
  display: block;
}

.test {
  position: fixed;
  z-index: 1;
  right: 100px;
  top: 100px;
  @media #{media("<")} {
    right: 50px;
    top: 50px;
  }

  pointer-events: all;
}

.title {
  margin-bottom: 10px;
}

.buttons {
  display: flex;
  gap: 10px;
}

.btn {
  padding: 10px 15px;

  cursor: pointer;
}

.wrapper {
  display: contents;
}
.qwe {
  display: contents;
}