@import 'src/components/styles/helpers.scss';

.wrapper {
  padding-left: calc(#{$col-part-d} * 3);
  @media #{media("<")} {
    padding-left: 0;
    margin-top: rem(24px);
  }
}

.top {
  @media #{media("<", m)} {
    display: flex;
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: rem(100px);
  column-gap: $col-gap-d;
  @media #{media("<")} {
    row-gap: rem(24px);
    column-gap: rem(19px);
  }
}

.mobile {
  @media #{media(">", m)} {
    display: none;
  }
  width: calc(#{$col-part-m} * 4);
}