@import 'src/components/styles/helpers.scss';


.row {
  display: flex;
  justify-content: space-between;
  @media #{media("<")} {
    display: block;
  }
}

.col {

  &:nth-child(2) {
    width: rem(1451px);
    @media #{media("<")} {
      margin-top: rem(24px);
      width: 100%;
    }
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: rem(9px);
  row-gap: rem(120px);
  @media #{media("<")} {
    row-gap: 0;
    grid-template-columns: repeat(6, 1fr);
  }
  @media #{media("<", m)} {
    grid-template-columns: repeat(3, 1fr);
    row-gap: rem(24px);
  }
}

.item {
  position: relative;
  z-index: 0;

  width: rem(283px);
  height: rem(45px);
  @media #{media("<")} {
    width: rem(115px);
    height: rem(29px);
  }
  @media #{media("<", m)} {
    width: rem(108px);
  }
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  overflow: hidden;
  mix-blend-mode: darken;

  svg {
    width: rem(283px);
    height: rem(45px);
    @media #{media("<")} {
      width: rem(115px);
      height: rem(29px);
    }
    @media #{media("<", m)} {
      width: rem(108px);
    }
  }

  &.big {
    height: rem(74px);
    @media #{media("<")} {
      height: rem(36px);
    }
    svg {
      height: rem(74px);
      @media #{media("<")} {
      height: rem(36px);
    }
    }
  }
}

.image {
}

.svg {
  position: absolute;
  left: 0;
  top: 0;

  display: flex;
  align-items: center;
  justify-content: center;
}