@import 'src/components/styles/helpers.scss';

.icon {
  margin-bottom: rem(20px);
  @media #{media("<")} {
    margin-bottom: rem(4px);
  }

  display: flex;
  align-items: center;
  justify-content: flex-start;

  svg {
    --size: #{rem(103px)};
    @media #{media("<")} {
      --size: #{rem(48px)};
    }
    
    width: var(--size);
    height: var(--size);
  }
}

.count {
  letter-spacing: -0.03em;
  @include fsl(60px, 85%);
  @media #{media("<")} {
    @include fsl(32px);
  }
}