@import 'src/components/styles/helpers.scss';

.intro {
  position: relative;
  z-index: 0;
  @media #{media("<")} {
    margin-top: rem(64px);
  }
}

.list {
  position: absolute;
  top: 22.9vh;
  left: calc(#{$col-part-d} * 8 + #{$col-pad-d});
  @media #{media("<")} {
    position: static;
  }
  counter-reset: index;
}

.item {
  position: relative;
  z-index: 0;
  padding-top: rem(6px);
  padding-left: rem(14px);

  @include fsl(32px);
  letter-spacing: -0.04em;
  counter-increment: index;
  @media #{media("<")} {
    @include fsl(24px);
    letter-spacing: -0.01em;
  }

  &::before {
    content: "0" counter(index);
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;

    @include fsl(12px);
    @media #{media("<")} {
      @include fsl(8px);
    }
  }

  &:not(:first-child) {
    margin-top: rem(12px);
    @media #{media("<")} {
      margin-top: rem(6px);
    }
  }
}

.bot {
  padding-top: 44.7vh;
}

.showreel {
  margin-top: rem(45px);
  @media #{media("<")} {
    margin-top: rem(32px);
  }
  @media #{media("<", m)} {
    margin-top: rem(16px);
  }
}

.title {
  @include fsl(210px, 85%);
  letter-spacing: -0.03em;
  @media #{media("<")} {
    @include fsl(154px);
    letter-spacing: -0.04em;
  }
  @media #{media("<", m)} {
    @include fsl(72px);
    letter-spacing: -0.03em;
  }
}

.test {
  transform-origin: 50% 50%;
}

.flex {
  display: flex;
  perspective: 40rem;
  perspective-origin: -10% 50%;
}

.links {
  margin-top: rem(28px);

  display: flex;
  justify-content: space-between;
}

.link {
  @include fsl(12px, 100%);
  letter-spacing: -0.01em;
}

.content {
  margin-top: rem(32px);
}

.row {
  margin-top: 35.1vh;
  @media #{media("<", m)} {
    margin-top: 34.1vh;
  }

  display: flex;
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    @media #{media("<")} {
      width: calc(#{$col-part-m} * 9);
    }
    @media #{media("<", m)} {
      width: calc(#{$col-part-m} * 8);
    }
  }
  &:nth-child(2) {
    @media #{media("<")} {
      display: flex;
    }
  }
}