@import 'src/components/styles/helpers.scss';

.menu {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;

  width: 100%;
  height: 100vh;
  opacity: 0;
  pointer-events: none;
  
  background: rgba(#000, .2);
  transition: $transition;

  &.active {
    opacity: 1;
    pointer-events: all;

    .container {
      transform: none;
    }
  }
}

.container {
  padding: rem(68px) rem(16px) rem(32px);

  background: color(white);
  transform: translateY(-100%);
  transition: transform $transition;
}

.text {
  @include fsl(28px, 100%);
  letter-spacing: -0.01em;
}

.row {
  margin-top: rem(32px);

  display: flex;
}

.col {
  &:nth-child(1) {
    flex-shrink: 0;
    width: calc(#{$col-part-m} * 6);
  }
}

.link {
  @include fsl(14px, 100%);
  letter-spacing: -0.01em;
  border-bottom: 1px solid color(black);
}

.btn {
  margin-top: rem(32px);
}

.item {

  &:not(:first-child) {
    margin-top: rem(6px);
  }
}