@import 'src/components/styles/helpers.scss';

.button {
  padding: rem(7px) rem(9px) rem(7px) rem(24px);

  display: flex;
  align-items: center;

  @include fsl(16px, 100%);
  @include f-main;
  color: color(white);
  background: color(blue);
  border-radius: rem(30px);
  cursor: pointer;
  border: none;
  transition: $transition;

  &:hover {
    background: color(black);

    .svg {
      transform: scale(1);

      svg {
        opacity: 1;
      }
    }
  }

  &.default {
    padding: rem(16px) rem(24px);
  }
}


.svg {
  --size: #{rem(35px)};
  margin-left: rem(7px);

  width: var(--size);
  height: var(--size);
  display: flex;
  align-items: center;
  justify-content: center;

  background: color(white);
  border-radius: 50%;
  transition: $transition;
  transform-origin: center;
  transform: scale(0.3);
  
  svg {
    --size: #{rem(13px)};

    width: var(--size);
    height: var(--size);

    opacity: 0;
    transition: $transition;
  }
}