@use "sass:map";
@use "sass:string";
@use "sass:math";

@import './mixins.scss';
@import './functions.scss';

// fonts
$fs: 16px;

$ff-main: 'AeonikPro-Regular', sans-serif;

// media breakpoints
$breakpoints: (
  xxl: 1920px,
  xl: 1680px,
  ll: 1440px,
  l: 1366px,
  d: 1280px,
  t: 1024px,
  m: 768px,
  s: 576px,
  xs: 375px,
  xxs: 320px,
);

// variables
$colors: (
  black: #0B0B0B,
  white: #fff,
  blue: #0000F2,
  gray: #EBEBEB,
  primary-gray: #767676,
  light-gray: #F0F0F0,
  dark-gray: #8A8A8A,
  dark-white: #F3F3F3,
  borders: #D8D8D8,
);

@function color($key) {
  @return map.get($colors, $key);
}

// grid columns
$col-count: 12;

$col-pad-m: 16px;
$col-pad-d: 32px;

$col-gap-d: 32px;
$col-gap-m: 8px;

$col-width-m: '((100vw - (#{$col-pad-m} * 2) - (#{$col-gap-m} * #{$col-count - 1})) / #{$col-count})';
$col-width-d: '((100vw - (#{$col-pad-d} * 2) - (#{$col-gap-d} * #{$col-count - 1})) / #{$col-count})';

$col-part-m: '(#{$col-width-m} + #{$col-gap-m})';
$col-part-d: '(#{$col-width-d} + #{$col-gap-d})';


$transition-timing-function: cubic-bezier(0.33, 1, 0.68, 1);
$transition-duration: .5s;
$transition: $transition-duration $transition-timing-function;