@import 'src/components/styles/helpers.scss';

.projects {
  padding-bottom: rem(180px);
  @media #{media("<")} {
    padding-bottom: rem(60px);
  }
  // background: color(white);
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: $col-gap-d;
  row-gap: rem(100px);
  @media #{media("<")} {
    column-gap: $col-gap-m;
    row-gap: rem(24px);
  }
  @media #{media("<", m)} {
    grid-template-columns: 1fr;
  }
}

.container {
  height: rem(560px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @media #{media("<")} {
    height: auto;
  }
}

.title {
  letter-spacing: -0.03em;
  @include fsl(170px, 80%);
  @media #{media("<")} {
    @include fsl(32px);
  }

  br {
    @media #{media("<")} {
      display: none;
    }
  }
}

.wrapper {
  padding-left: calc(#{$col-part-d});
  @media #{media("<")} {
    padding-left: 0;
    margin-top: rem(24px);
  }
}

.content {
  @media #{media("<")} {
    grid-column: span 2;
  }
  @media #{media("<", m)} {
    grid-column: auto;
  }
}