.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  min-width: 320px;

  @media #{media("<")} {
    overflow: hidden;
  }
}

.main {
  position: relative;
  z-index: 1;

  background: color(dark-white);
}

.cursor-wrapper {
  --size: 22px;
  position: fixed;
  z-index: 1005;
  top: 0;
  left: 0;

  width: var(--size);
  height: var(--size);

  @media #{media("<")} {
    display: none;
  }

  mix-blend-mode: difference;
  pointer-events: none;

  &.expanded {
    mix-blend-mode: normal;

    .text-wrapper {
      opacity: 1;
    }
  }
}

.outer {
  --scale-ratio: 1;
  --size: 22px;
  position: absolute;
  top: 15%;
  left: -35%;

  display: block;
  width: var(--size);
  height: var(--size);

  background: color(white);
  border-radius: 50%;
}

.text-wrapper {
  --size: 143px;
  position: absolute;
  top: 15%;
  left: -65px;
  
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--size);
  height: var(--size);

  
  opacity: 0;
  @include fsl(32px, 100%);
  color: color(black);
}

.js-custom-cursor {
  * {
    cursor: none !important;
  }
}

.cursor-svg {
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(45px)};

    width: var(--size);
    height: var(--size);
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;

  background: color(white);
  transition: opacity 0.5s linear 0.3s;

  &.hidden {
    opacity: 0;
    pointer-events: none;
  }
}

.scroll-container {
  position: fixed;
  left: 0;
  top: 0;
  z-index: -1;
  // z-index: 1000;

  width: 100vw;
  height: 100vh;

  pointer-events: none;
  overflow-y: scroll;
}

.scroll-body {
  width: 100%;
}