@import 'src/components/styles/helpers.scss';

.modal {
  --pad: 0;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  padding: rem(32px);
  @media #{media("<")} {
    padding: 0;
  }

  width: 100vw;
  height: 100vh;

  opacity: 0;
  pointer-events: none;
  transition: $transition;

  &.active {
    opacity: 1;
    pointer-events: all;
  }
}

.container {
  position: relative;
  z-index: 0;
  padding: rem(32px);
  @media #{media("<")} {
    padding: rem(68px) rem(16px) rem(32px);
  }
  height: 100%;

  background: color(white);
}

.close {
  --pad: #{rem(17px)};

  position: absolute;
  top: var(--pad);
  right: var(--pad);
  z-index: 1;

  display: flex;
  align-items: center;
  justify-content: center;
  @media #{media("<")} {
    display: none;
  }

  cursor: pointer;
  outline: none;
  border: none;
  background: none;

  svg {
    --size: #{rem(45px)};

    width: var(--size);
    height: var(--size);
  }
}

.row {
  height: 100%;
  display: flex;
  justify-content: space-between;
  @media #{media("<")} {
    display: block;
  }
}

.col {
  flex-shrink: 0;
  width: calc(50% - 16px);
  @media #{media("<")} {
    width: auto;
  }
  &:nth-child(2) {
    display: flex;
    flex-direction: column;
    @media #{media("<")} {
      margin-top: rem(16px);
      display: block;
    }
  }
}

.title {
  letter-spacing: -0.03em;
  @include fsl(160px, 85%);
  @media #{media("<")} {
    @include fsl(32px);

    br {
      display: none;
    }
  }
}

.text {
  max-width: rem(686px);
  @media #{media("<")} {
    max-width: none;

    @include fsl(16px);
  }
  letter-spacing: -0.01em;
  @include fsl(24px, 130%);
}

.form {
  @media #{media("<")} {
    margin-top: rem(32px);
    
    width: auto;
    display: block;
  }
  width: calc(#{$col-part-d} * 5 - #{$col-gap-d});
  height: 100%;
  display: flex;
  align-items: center;
}

.response {
  margin-top: rem(261px);
  @media #{media("<")} {
    margin-top: 0;
  }
}

.btn {
  margin-top: rem(24px);
  @media #{media("<")} {
    margin-top: rem(16px);
  }
}