@import 'src/components/styles/helpers.scss';

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  width: 100vw;
  height: 100vh;
}

.video {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  min-width: 100vw;
  min-height: 100vh;
}

.bottom {
  padding: 0 64px;
  position: absolute;
  bottom: rem(32px);
  left: 0;
  right: 0;
  z-index: 1;

  display: flex;

}

.wrapper {
  transition: 0.6s $transition-timing-function;
}

.time {
  margin-bottom: rem(32px);
  
  display: flex;
  justify-content: flex-end;

  color: color(white);
  white-space: nowrap;
  letter-spacing: -0.04em;
  @include fsl(32px, 100%);
}

.progress {
  width: 100%;
  height: 1px;

  background: color(white);
}