@import 'src/components/styles/helpers.scss';

.label {
  position: relative;
  z-index: 0;

  display: flex;
  align-items: center;

  cursor: pointer;
}

.input {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;

  opacity: 0;
  pointer-events: none;
}

.left {
  margin-right: rem(16px);
}

.btn {
  cursor: pointer;
}

.icon {
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(32px)};

    width: var(--size);
    height: var(--size);
  }
}

.item {
  display: flex;
  align-items: center;
}