@import 'src/components/styles/helpers.scss';

.item {
  padding: rem(8px) rem(20px);
  @media #{media("<")} {
    padding: rem(4px) rem(12px);
  }
  border-radius: rem(20px);
  background: color(gray);
  letter-spacing: -0.01em;
  transition: $transition;
  cursor: pointer;
  @include fsl(20px, 130%);
  @media #{media("<")} {
    @include fsl(12px);
  }

  &:hover {
    background: color(blue);
    color: color(white);
  }

  &.active {
    background: color(blue);
    color: color(white);
  }

  &.small {
    padding: rem(6px) rem(16px);

    cursor: default;
    @include fsl(16px);

    &:hover {
      background: color(gray);
      color: color(black);
    }
  }
}