@import 'src/components/styles/helpers.scss';

.container {
  position: relative;
  z-index: 0;

  display: flex;
  align-items: center;
  height: rem(561px);
  @media #{media("<")} {
    // height: rem(224px);
    height: auto;
  }
}

.item {
  width: calc(#{$col-part-d} * 6 - #{$col-gap-d});
  @media #{media("<")} {
    width: auto;
  }
}

.bot {
  margin-top: rem(20px);
  @media #{media("<")} {
    margin-top: rem(12px);
  }
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: $col-gap-d;
  @media #{media("<")} {
    display: block;
  }
}

.col {
  &:nth-child(2) {
    @media #{media("<")} {
      margin-top: rem(4px);
    }
  }
}

.video {
  width: 100%;
}

.content {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  @media #{media("<")} {
    position: static;

    display: flex;
    align-items: center;
  }
  width: 100%;
  height: 100%;
}

.slider {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;

  width: 100%;
  height: 100%;
}

.title {
  @include fsl(32px, 100%);
  letter-spacing: -0.04em;
  @media #{media("<")} {
    @include fsl(20px);
  }
}

.list {
  margin-top: rem(20px);
  @media #{media("<")} {
    margin-top: rem(12px);
  }
}