@import 'src/components/styles/helpers.scss';

$header-zIndex: 998;

.header {
  position: fixed;
  z-index: $header-zIndex;
  left: 0;
  right: 0;
  top: 0;
  mix-blend-mode: difference;
}

.inner {
  padding: rem(32px) 0 rem(52px);
  @media #{media("<")} {
    padding: rem(16px) 0;
    position: relative;
    z-index: 2;
  }
}

.row {
  display: flex;
  @media #{media("<")} {
    justify-content: space-between;
    align-items: center;
  }
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    width: calc(#{$col-part-d} * 3);
    @media #{media("<")} {
      width: auto;
    }
  }

  &:nth-child(2) {
    width: calc(#{$col-part-d} * 5 - #{$col-gap-d});
    @media #{media("<")} {
      width: auto;
    }
  }

  &:nth-child(3) {
    flex-grow: 1;

    display: flex;
    justify-content: space-between;
  }
}

.logo {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  svg {
    width: rem(205px);
    height: rem(29px);
    @media #{media("<")} {
      width: rem(146px);
      height: rem(20px);
    }
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
  gap: rem(32px);
}

.item {

  &:not(:first-child) {
    @media #{media("<")} {
      margin-top: 4px;
    }
  }
}

.link {
  position: relative;
  z-index: 0;

  &::before {
    content: "";
    position: absolute;
    top: calc(100% + 1px);
    left: 0;

    width: 100%;
    height: 1px;

    background: color(white);
    pointer-events: none;
    transform-origin: 100% 50%;
    transform: scale3d(0, 1, 1);
    transition: transform 0.3s;
  }

  &:hover::before {
    transform-origin: 0% 50%;
    transform: scale3d(1, 1, 1);
  }
}

.text {
  @include fsl(20px, 100%);
  color: color(white);
  letter-spacing: -0.01em;
  @media #{media("<")} {
    color: color(black);
    @include fsl(16px);
  }
}

.textUs {
  margin-bottom: 5px;

  display: flex;
  align-items: center;
}

.right {
  position: relative;
  z-index: 0;

  width: rem(205px);
  height: rem(52px);
}

.button {
  position: fixed;
  top: rem(32px);
  right: rem(32px);
  z-index: $header-zIndex + 1;

  @media #{media("<")} {
    display: none;
  }
}

.icon {

  width: $col-gap-d;
  display: flex;
  align-items: center;
  justify-content: center;
  @media #{media("<")} {
    margin-right: rem(8px);
    width: rem(17px);
  }

  svg {
    width: rem(17px);
    height: rem(14px);
    @media #{media("<")} {
      path {
        fill: color(black);
      }
    }
  }
}

.wrapper {
  margin-left: $col-gap-d;
}

.desktop {
  @media #{media("<")} {
    display: none;
  }
}

.mobile {
  @media #{media(">")} {
    display: none;
  }
}

.menu {
  width: rem(44px);
  height: rem(10px);
  display: flex;
  align-items: center;
  justify-content: center;

  cursor: pointer;
}

.burger {
  position: relative;
  z-index: 0;

  width: 100%;
  height: rem(10px);

  border-top: 2px solid color(white);
  border-bottom: 2px solid color(white);
  transition: height 0.4s 0.4s $transition-timing-function, border-color 0.4s 0.4s $transition-timing-function;


  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: rem(24px);
    height: 2px;

    transition: all 0.4s $transition-timing-function;
    background: none;
  }

  &.active {
    height: 0;
    border-color: transparent;
    transition: all 0.35s 0s $transition-timing-function;

    &:before {
      transform: translate(-50%, -50%) rotate(-45deg);
      background: color(white);
      transition-delay: 0.5s;
    }
    
    &:after {
      transform: translate(-50%, -50%) rotate(-135deg);
      background: color(white);
      transition-delay: 0.5s;
    }
    // &::before,
    // &::after {
    //   transform: translate(0, -50%) rotate(-135deg);
    //   transition-delay: 0.5s;
    //   background: color(white);
    // }
  }
}
