@import 'src/components/styles/helpers.scss';

.footer {
  // position: fixed;
  // bottom: 0;
  // left: 0;
  // right: 0;
  
  background: color(black);
}

.footerWrapper {
  position: relative;
  z-index: 10;

  overflow: hidden;
  background: color(black);
}

.inner {
  padding: rem(64px) 0 rem(32px);
  @media #{media("<")} {
    padding: rem(16px) 0;
  }
}

.row {
  display: flex;
  @media #{media("<")} {
    justify-content: space-between;
  }
  @media #{media("<", m)} {
    display: block;
  }
}

.col {
  flex-shrink: 0;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &:nth-child(1) {
    width: calc(#{$col-part-d} * 3);
    @media #{media("<")} {
      width: calc(#{$col-part-m} * 6 - #{$col-gap-m});
    }
    @media #{media("<", m)} {
      width: auto;
    }
  }

  &:nth-child(2) {
    width: calc(#{$col-part-d} * 3);
    @media #{media("<")} {
      width: calc(#{$col-part-m} * 6 - #{$col-gap-m});
    }
    @media #{media("<", m)} {
      margin-top: rem(16px);
      width: auto;
    }
  }

  &:nth-child(3) {
    flex-grow: 1;
  }
}

.list {
  @media #{media("<")} {
    display: flex;
  }
}

.item {
  display: flex;

  &:not(:first-child) {
    margin-top: rem(4px);
    @media #{media("<")} {
      margin-top: 0;
      margin-left: rem(16px);
    }
  }
}

.link {
  // padding-bottom: 2px;
  
  color: color(white);
  letter-spacing: -0.01em;
  @include fsl(32px, 130%);
  @media #{media("<")} {
    @include fsl(16px);
  }
  border-bottom: 1px solid color(white);
}

.mid {
  margin: rem(180px) 0;
  @media #{media("<")} {
    margin: rem(48px) 0;
  }
}

.contacts {
  margin-bottom: rem(60px);
  @media #{media("<")} {
    margin-bottom: rem(20px);
  }
}

.contactsItem {
  display: block;

  &:not(:first-child) {
    margin-top: rem(16px);
    @media #{media("<")} {
      margin-top: rem(8px);
    }
  }
}

.ref {
  color: color(white);
  letter-spacing: -0.03em;
  @include fsl(60px, 85%);
  @media #{media("<")} {
    @include fsl(32px);
  }
}

.text {
  color: color(dark-gray);
  letter-spacing: -0.01em;
  @include fsl(20px, 130%);
  @media #{media("<")} {
    @include fsl(14px);
  }
}

.flex {
  display: flex;
  align-items: flex-end;
}

.bot {
  @media #{media("<")} {
    display: flex;
    justify-content: space-between;
  }
}

.left {
  flex-shrink: 0;
  width: calc(#{$col-part-d} * 2);
  @media #{media("<")} {
    width: calc(#{$col-part-m} * 6 - #{$col-gap-m});
  }
}

.right {
  @media #{media("<")} {
    width: calc(#{$col-part-m} * 6 - #{$col-gap-m});
  }
}

.icon {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.home {
  color: color(dark-gray);
  @include fsl(14px, 130%);
  letter-spacing: -0.01em;
}

.policy {
  color: color(white);
  letter-spacing: -0.01em;
  @include fsl(14px, 130%);
}

.desktop {
  @media #{media("<")} {
    display: none;
  }
}

.mobile {
  @media #{media(">")} {
    display: none;
  }
}
