@import 'src/components/styles/helpers.scss';

.showreel {
  padding: 0 $col-pad-d;
  @media #{media("<")} {
    padding: 0 $col-pad-m;
    height: 100vh;
  }
}

.row {
  position: relative;
  z-index: 0;

  display: flex;
  @media #{media("<")} {
    display: block;
    height: 100%;
  }
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    position: absolute;
    left: 0;
    top: 0;

    width: calc(#{$col-part-d} * 3);
    @media #{media("<")} {
      display: none;
    }
  }

  &:nth-child(2) {
    padding-left: 25.3%;
    width: 100%;
    @media #{media("<")} {
      padding-left: 0;
      height: 100%;
    }
    // width: calc(#{$col-part-d} * 9 - #{$col-gap-d});
    // width: 74.7%;
  }
}

.item {

  &:not(:first-child) {
    margin-top: rem(12px);
    @media #{media("<")} {
      margin-top: rem(6px);
    }
  }
}

.link {
  letter-spacing: -0.01em;
  border-bottom: 1px solid color(black);
  @include fsl(20px, 100%);
  @media #{media("<")} {
    @include fsl(16px);
  }
}

.container {
  position: relative;
  z-index: 0;

  height: calc(100vh - 64px);
  @media #{media("<")} {
    height: 100%;
  }
  
  background: color(black);
  overflow: hidden;
}

.wrapper {
  position: absolute;
  left: rem(32px);
  top: rem(24px);
  bottom: rem(32px);
  z-index: 3;
  @media #{media("<")} {
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;

    align-items: center;
    justify-content: center;
  }
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
}

.label {
  position: sticky;
  position: -webkit-sticky;
  bottom: rem(32px);
  z-index: 1;

  display: flex;
  align-items: center;
}

.icon {
  margin-right: rem(8px);

  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    width: rem(37px);
    height: rem(27px);
    @media #{media("<")} {
      width: rem(25px);
      height: rem(18px);
    }
  }
}

.text {
  color: color(white);
  @include fsl(72px, 100%);
  letter-spacing: -0.04em;
  @media #{media("<")} {
    @include fsl(48px);
  }
}

.overlay {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;

  background: color(black);
  opacity: 0.8;
}

.layout {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
}

.image {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  transform-origin: center;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}