@import 'src/components/styles/helpers.scss';

.row {
  display: flex;
  @media #{media("<")} {
    display: block;
  }

  &.alt {
    @media #{media("<")} {
      display: flex;
      justify-content: space-between;

    }
    .col {
      @media #{media("<")} {
        width: calc(50% - 4px);
      }

      &:nth-child(1) {
        @media #{media("<", m)} {
          display: none;
        }
      }

      &:nth-child(2) {
        @media #{media("<")} {
          margin-top: 0;
        }
        @media #{media("<", m)} {
          width: 100%;
        }
      }
    }
  }
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    width: calc(#{$col-part-d} * 3);
    @media #{media("<")} {
      width: auto;
    }
  }

  &:nth-child(2) {
    width: calc(#{$col-part-d} * 9 - #{$col-gap-d});
    @media #{media("<")} {
      margin-top: rem(32px);

      width: auto;
    }
  }
}

.label {
  color: color(primary-gray);
  letter-spacing: -0.04em;
  @include fsl(32px, 100%);
  @media #{media("<")} {
    @include fsl(16px);
  }
}